import { actionType, appStateType } from '../interfaces/stateInterfaces'
import initialState from './initialState'

export default function reducer (state: any, action: actionType): appStateType {
  switch (action.type) {
    case 'INIT':
      return {
        ...action.value
      }
    case 'SELECT_FROM':
      return {
        ...state,
        selectedFrom: action.value
      }
    case 'SELECT_TO':
      state.selectedTo[action.index] = action.value
      return {
        ...state
      }
    case 'RESET':
      return {
        ...state,
        selectedFrom: null,
        selectedTo: [false, false, false, false]
      }
    default:
      return state
  }
}
