import * as React from 'react'
import Box from '@mui/material/Box'
import CssBaseline from '@mui/material/CssBaseline'
import {
  Grid
} from '@mui/material'
import { ItemFrom } from './components/ItemFrom'
import { ItemTo } from './components/ItemTo'
import Footer from './components/Footer'
import Header from './components/Header'
import reducer from './reducer'
import { appStateType } from './interfaces/stateInterfaces'
import initialState from './reducer/initialState'

export default function App () {

  const [store, dispatch] = React.useReducer(reducer, initialState)

  React.useEffect(() => {
    const _temp = localStorage.getItem("boomzaState")
    if (_temp !== null) 
    dispatch(
      {
      type: 'INIT',
      value: JSON.parse(_temp),
      index: 0
    })

  }, [])

  React.useEffect(() => {
    localStorage.setItem('boomzaState', JSON.stringify(store))
  }, [store])

  const ref = React.useRef<HTMLDivElement>(null)

  return (
    <Box sx={{ pb: 7 }} ref={ref}>
      <CssBaseline enableColorScheme />
      <Header />

      <Grid container sx={{
        top: '5em',
        width: '100%',
        position: 'absolute',
        bottom: '4em',
        left: 0,
        textAlign: 'center'
      }}>

        <Grid item xs={6} sx={{ borderRight: 1, textAlign: 'center' }}>
          <ItemFrom key='1001' text='XXX' index={0} store={store} dispatch={dispatch} />
          <ItemFrom key='1002' text='YYY' index={2} store={store} dispatch={dispatch} />
        </Grid>


        <Grid item xs={6} sx={{
          borderLeft: 1, textAlign: 'center',
        }}>
          <ItemTo key='001' text='AAA' index={0} store={store} dispatch={dispatch} />
          <ItemTo key='002' text='BBB' index={1} store={store} dispatch={dispatch} />
          <ItemTo key='003' text='CCC' index={2} store={store} dispatch={dispatch} />
          <ItemTo key='004' text='DDD' index={3} store={store} dispatch={dispatch} />
        </Grid>
      </Grid>

      <Footer />
    </Box >
  )
}
