import {
  AppBar,
  Icon,
  IconButton,
  Toolbar,
  Typography
} from "@mui/material/"

import * as React from "react"

export default function Header() {
  return (
    <AppBar position="static">
      <Toolbar>
        <Icon
          color="inherit"
          aria-label="Flash Card"
          // onClick={() => window.open("https://boomza.com", "_system")}
        >
          filter_none
        </Icon>
        <Typography
          variant="subtitle2"
          color="inherit"
          width="100%"
        >
          &nbsp;
        </Typography>
        <IconButton color="inherit" aria-label="Menu">
          <Icon onClick={() => alert('Hello')} fontSize="large">
            blur_on
          </Icon>
        </IconButton>
      </Toolbar>
    </AppBar>
  )
};
