import React from 'react'
import { keyframes, styled } from '@mui/material/styles'
import { Paper } from '@mui/material'
import theme from '../theme'
import { DragDropContainer } from 'react-drag-drop-container'
import { appStateType } from '../interfaces/stateInterfaces'

interface ItemProps {
  text: string
  index: number
  store: appStateType
  dispatch: any
}

export const StyledItemTo: any = styled(Paper)(({ theme }) => ({
  position: 'relative',
  textAlign: 'center',
  transition: 'background-color .8s ease-out',
  color: theme.palette.text.primary,
  borderRadius: '2em',
  padding: 15,
  width: '40vw',
  display: 'block',
  marginLeft: 'auto',
  borderStyle: 'solid',
  marginRight: 'auto',
  marginTop: '1em',
  ":hover": {
    backgroundColor: theme.palette.background.default,
    transition: 'background-color .1s ease-in',
    animation: `${shake} 100ms infinite ease`
  }

}))

const shake = keyframes`
0% {
  width: 85%
},
50% {
  width: 84%
},
100% {
  width: 85%
}`

export const ItemTo: React.FC<ItemProps> = ({ text, index, store, dispatch }) => {

  const fnOnClick = () => {
    if (store.selectedFrom === null || store.selectedFrom === index) {
      dispatch({
        type: 'RESET',
        value: null
      })
    } else {
      dispatch({
        type: 'SELECT_TO',
        value: true,
        index
      })
    }
  }
  return (
    <StyledItemTo
      onClick={fnOnClick}
      sx={{
        animation:
          store.selectedTo[index]
            ? `${shake} 100ms infinite ease`
            : ``,
        backgroundColor: store.selectedTo[index]
          ? theme.palette.background.default
          : theme.palette.background.paper
      }}>
      {text}
    </StyledItemTo>
  )
}

export default ItemTo

