import React, { useState } from "react"
import theme from '../theme'
import { StyledItem } from './StyledItem'
import { DragDropContainer } from 'react-drag-drop-container'
import { appStateType } from '../interfaces/stateInterfaces'


interface ItemProps {
  text: string
  index: number
  store: appStateType
  dispatch: any
}

export const ItemFrom: React.FC<ItemProps> = ({ text, index, store, dispatch }) => {
  return (
    <DragDropContainer targetKey={index} >

      <StyledItem
        onClick={() => {
          dispatch({
            type: 'RESET',
            value: null
          })

          dispatch({
            type: 'SELECT_FROM',
            value: index
          })
        }}
        style={{
          userSelect: "none",
          backgroundColor: store.selectedFrom === index
            ? theme.palette.background.default
            : theme.palette.background.paper
        }}>
        {text}
      </StyledItem>
    </DragDropContainer>
  )
}

export default ItemFrom